export default {
  props: {
    filtersOpen: {
      type: Boolean,
      default: false
    },
    collectionView: {
        type: Boolean,
        default: false
    },
    isEscape: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      filterData: {
        types: [
          {
            name: "Under Bounipun",
            value: "under-bounipun",
            checked: false
          },
          {
            name: "Third Party",
            value: "third-party",
            checked: false
          }
        ],
        availabilityTypes: [
          {
            name: "Ready To Ship",
            value: "ready-to-ship",
            checkd: false
          }
          // {
          //   name: "Made to Order",
          //   value: "made-to-order",
          //   checkd: false
          // }
        ],
        priceRanges: [
          {
            name: "Under ₹ 9999",
            value: "<9999"
          },
          {
            name: "Under ₹ 14999",
            value: "<14999"
          },
          {
            name: "Under ₹ 19999",
            value: "<19999"
          },
          {
            name: "Under ₹ 24999",
            value: "<24999"
          },
          {
            name: "Under ₹ 29999",
            value: "<29999"
          },
          {
            name: "Under ₹ 34999",
            value: "<34999"
          }
        ],
        selectedPriceRange: "",
        collections: [],
        variants: [],
        baseColors: []
      }
    };
  },
  mounted() {
    this.fetchFilterData();
  },
  watch: {
    filterData: {
      handler(newValue) {
        this.$emit("updated", newValue);
      },
      deep: true,
      immediate: true
    },
    $route() {
      this.clearAllFilters();
    }
  },
  methods: {
    /* clear all filters */
    clearAllFilters() {
      /* uncheck all filters */
      const filterKeys = [
        "availabilityTypes",
        "collections",
        "variants",
        "baseColors"
      ];
      filterKeys.forEach(filterKey => {
        this.filterData[filterKey].forEach(option => (option.checked = false));
      });
      /* reset selected price range */
      this.filterData.selectedPriceRange = "";
      // this.clearSort();
      this.$emit("clearSort");
    },
    async fetchFilterData() {
      /* fetch type of products */
      /* fetch collections */
      /* fetch variants */
      /* fetch base colors */
      const filtersFetch = this.$axios.$get("/getSearchFilters");
      /* wait for request to complete */
      const { response, error } = await this.$task(filtersFetch);
      /* if error occurred */
      if (error || response.fetched === false) {
        console.log("could not fetch search filters");
        return;
      }

      this.filterData.collections = response.collections.map(collection => ({
        ...collection,
        value: collection._id,
        checked: false
      }));

      /* set base colors (for product cards)*/
      this.baseColors = response.baseColors;

      this.filterData.baseColors = response.baseColors.map(color => ({
        ...color,
        value: color.name,
        checked: false
      }));
      this.filterData.variants = response.variants.map(variant => ({
        ...variant,
        value: variant._id,
        checked: false
      }));

      /* TODO: temporary fix: filter out decor variants */
      this.filterData.variants = this.filterData.variants.filter(variant => ['SQUARE', 'SHAWL', 'STOLE'].includes(variant.name.toUpperCase()) );

      // this.filterData.variants.forEach(f => {
      //   const name = f.name.toUpperCase();
      //   console.log(name,'-VARIANTS-')
      //   if(name === "BASIC" || name==="CLASSIC" || name === "PREMIUM") {
      //    f.name = `${f.name} - Décor`
      //   }
      //   return f
      // })

      this.$emit('dataFetched');
    }
  }
};
